.tablecell {
    font-family: 'Lato', sans-serif;
    border: none;
    max-width: 150px; 
    overflow-x: hidden;
    cursor:pointer;
}

.tableheading{
    font-family: 'Lato', sans-serif;
    opacity: 1;
}

.heightlimit{
    font-family: 'Lato', sans-serif;
    height:50px; 
    overflow:hidden;
}

#tablepretty{
    white-space:nowrap;
    table-layout: fixed;
    width:auto
}
